import SigninPresenter from './SigninPresenter';
import { useCallback, useState } from 'react';
import { useNavigate } from 'react-router';
import axios from 'axios';
// @ts-ignore
import { getCookie, setCookie, removeCookie } from 'Cookie.ts';
import React from 'react';


function SigninConatiner () {
    const navigate = useNavigate();
    let [id, setId] = useState<string>("");
    let [pw, setPw] = useState<string>("");

    function auth(){
        if(getCookie('USER')){
        window.alert('    .');
        window.location.replace('/');
        return false;
        }else{
          return true;
        };
      }

    function goStart():void{
        const regx = /[-|-|-]/;
        const space = /\s/g; 
        if(((id.length>0) && (pw.length>0))&& !regx.test(id)&& !regx.test(pw) && !id.match(space) && !pw.match(space)){
        axios.post("http://13.125.81.51:3003/apis/auth/signIn", {
            loginid: id,
            password: pw
        },{withCredentials:true,
           //httpsAgent : {agent} 
        })
        .then((response) => 
            {
            window.alert('setcookie start');
            if (response.data.token) {
                console.log(response.data.token);
                setCookie('USER',response.data.token,{
                    path:"/",
                    secure:false,
                    sameSite:"lax",
                });
                localStorage.setItem('refreshToken',response.data.refreshToken);
            window.alert('setcookie completed');
            console.log('Login completed');
            navigate('/');
            } else {
            window.alert("");
            };
        })
        .catch((error) => {
            console.log(error);
        }); //   JWT   

        }else{
        window.alert("check plz");
        }
    }

    const idChange = useCallback(
        (e) => {
            setId(e.target.value);
        },
        [id]
    )

    const pwChange = useCallback(
        (e) => {
            setPw(e.target.value);
        },
        [pw]
    )

    return (
        <SigninPresenter
        auth={auth}
        id = {id}
        pw = {pw}
        setId={setId}
        setPw = {setPw}
        idChange = {idChange}
        pwChange = {pwChange}
        goStart = {goStart} />
    )
}

export default SigninConatiner;