import Footer from "components/Footer";
import GetLogo from "components/GetLogo";
import GetTeamname from "components/GetTeamname";
import GetTeamnameEng from "components/GetTeamnameEng";
import Header from "components/Header";
import MainWrapper from "components/MainWrapper";
import NavBar from "components/NavBar";
import React from "react";
import styled from "styled-components";
import {ReactTinyLink} from 'react-tiny-link';

const HeadLine1 = styled.h1`
    font-size:25px;
    margin:0;
    padding:15px;
    width:100%;
`;

const Section = styled.div`
    width:100%;
    padding:10px;
    margin-bottom:25px;
`;

const SmallSection1 = styled.div`
    display:inline-block;
    padding:10px;
    margin-bottom:25px;
`;

const SmallSection2 = styled.div`
    position:absolute;
    padding:75px;
    display:inline-block;
`;

const Line = styled.p`
    font-size:20px;
    margin : 15px 0;
    padding-top:25px;
`;

function MyteamPresenter({
    user,
    userDetail,
    teamInfo,
    career,
    squad,
    stad,
}:{
    user:any,
    userDetail:any,
    teamInfo:any,
    career:any,
    squad:string[],
    stad:any,
}){
    return (
        <>
        <Header />
        <NavBar/>
        <MainWrapper>
        <Section>
        <SmallSection1>
        <HeadLine1>Myteam</HeadLine1>
        <HeadLine1>{GetTeamname(userDetail?.myteam.toString())}</HeadLine1>
        <img src = {GetLogo(GetTeamnameEng(userDetail?.myteam.toString()))} height='200px'/>
        </SmallSection1>

        <SmallSection2>
        <Line> : {stad?.region}</Line>
        <Line> : {stad?.stadium}</Line>
        <Line> : {stad?.manager}</Line>
        </SmallSection2>
        </Section>

        <Section>
            <h3> </h3>
            {teamInfo.homepage ? 
            <ReactTinyLink
            cardSize='large'
            header={GetTeamname(userDetail?.myteam.toString())+'  '}
            showGraphic={false}
            maxLine={3}
            minLine={1}
            url={teamInfo.homepage.toString()}
            description='Linked by HoomBa'
            />:<a href={teamInfo.homepage} target='_blank'> </a>}
        </Section>      
        
        <Section>
            <h3> </h3>
            {teamInfo.store ? 
            <ReactTinyLink
            cardSize='large'
            header={GetTeamname(userDetail?.myteam.toString())+'  '}
            showGraphic={false}
            maxLine={3}
            minLine={1}
            url={teamInfo.store.toString()}
            description='Linked by HoomBa'
            />:<a href={teamInfo.store} target='_blank'> </a>}
        </Section>
        
        <Section>
            <h3> </h3>
            {teamInfo.youtube ? 
            <ReactTinyLink
            cardSize='large'
            header={GetTeamname(userDetail?.myteam.toString())+'  '}
            showGraphic={false}
            maxLine={3}
            minLine={1}
            url={teamInfo.youtube.toString()}
            description='Linked by HoomBa'
            />:<a href={teamInfo.youtube} target='_blank'> </a>}
            {/* <a href={teamInfo.youtube} target='_blank'> </a> */}
        </Section>

        <Section>
        <h3></h3>
        PL  : {career.PL}  <br/>{career.PL === 0 ? '':'('+career.PL_YEAR+')'}<br/>
        UCL  : {career.UCL}  <br/>{career.UCL === 0 ? '':'('+career.UCL_YEAR+')'}<br/>
        UEL  : {career.UEL}  <br/>{career.UEL === 0 ? '':'('+career.UEL_YEAR+')'}<br/>
        FA  : {career.FA}  <br/>{career.FA === 0 ? '':'('+career.FA_YEAR+')'}<br/>
           : {career.EFL}  <br/>{career.EFL === 0 ? '':'('+career.EFL_YEAR+')'}<br/>
        </Section>
 
        <Section>
        <h3> (21-22)</h3>
        <div>
        {squad.map((
            squaddata:string
            )=>(
            <img src={squaddata} width='750px'/>
        ))}
        </div>
        </Section>

        </MainWrapper>
        <Footer />
        </>
    );
}

export default MyteamPresenter;