import MyteamPresenter from './MyteamPresenter';
import { useNavigate } from 'react-router';
// @ts-ignore
import { getCookie, removeCookie, setCookie } from 'Cookie.ts';
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import GetSquad from 'components/GetSquad';

function MyteamConatiner () {
    const navigate = useNavigate();
    const [user,setUser] = useState<any>([]);
    const [userDetail,setUserDetail] = useState<any>(null);
    let [teamInfo, setInfo] = useState([]);
    const [stad, setStad] = useState<any>(null);
    let [career, setCareer] = useState([]);
    const [squad, setSquad] = useState<string[]>([]);

    function auth(){
        if(getCookie('USER')){
          return true;
        }else{
          window.alert('');
          window.location.replace('/');
          return false;
        };
      }

      useEffect(()=>{
        if(auth()){
          axios.post('http://13.125.81.51:3003/apis/auth/authToken', {
            token:getCookie('USER')
        },{withCredentials:true
        })
        .then((response)=>{
          if(response.data !== -3){
            console.log(response.data);
            setUser(response.data);
          }else if((response.data === -3) && (localStorage.refreshToken)){
            window.alert('refreshing...');
            axios.post('http://13.125.81.51:3003/apis/auth/refreshToken', {
            refreshToken:localStorage.refreshToken
            },{withCredentials:true
            }).then((res)=>{
              if(res.data !== -3){
                window.alert('refreshed');
                setCookie('USER',res.data.token,{
                path:"/",
                secure:false,
                sameSite:"lax",
              });
              localStorage.setItem('refreshToken',res.data.refreshToken);
              window.location.reload();
              }else{
                removeCookie('USER');
                localStorage.removeItem('refreshToken');
                alert(' .   ');
                window.location.replace('/');
              }
            });
          }
          });
        }
      },[auth()]);

    useEffect(()=>{
        if(user){
        axios.post('http://13.125.81.51:3003/apis/user/getUserDetail', {
        userid:user.user_id
        },{withCredentials:true})
        .then((res)=>{
            setUserDetail(res.data[0]);
        });
    } 
    },[user]);

    useEffect(()=>{
      if(userDetail){
        setSquad(GetSquad(userDetail.myteam.toString()));
        console.log(squad);
        axios.post('http://13.125.81.51:3003/apis/football/getTeamInfo',{
          teamid:userDetail.myteam
        },{withCredentials:true})
        .then((res)=>{
          setInfo(res.data[0]);
        });

        axios.post('http://13.125.81.51:3003/apis/football/getTeamCareer',{
          teamid:userDetail.myteam
        },{withCredentials:true})
        .then((res)=>{
          setCareer(res.data[0]);
        });

        axios.post('http://13.125.81.51:3003/apis/football/getStadiumInfo',{
          teamid:userDetail.myteam.toString()
        },{withCredentials:true})
        .then((response)=>{
          setStad(response.data[0]);
        })
      }
    },[userDetail])

    return (
        <MyteamPresenter
        user={user}
        userDetail={userDetail}
        teamInfo = {teamInfo}
        career = {career}
        squad = {squad}
        stad = {stad} />
    )
}

export default MyteamConatiner;