import axios from 'axios';
import { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router';
import CommunityDetailPresenter from './CommunityDetailPresenter';
// @ts-ignore
import { getCookie, removeCookie, setCookie } from 'Cookie.ts';
import React from 'react';

function CommunityDetailConatiner () {
    const navigate = useNavigate();
    const [user,setUser] = useState<any>([]);
    const [userDetail,setUserDetail] = useState<any>(null);
    const [post, setPost] = useState(null);
    const params = useParams();

    function auth(){
        if(getCookie('USER')){
          return true;
        }else{
          window.alert('');
          window.location.replace('/');
          return false;
        };
      }
      
      useEffect(()=>{
        if(auth()){
          axios.post('http://13.125.81.51:3003/apis/auth/authToken', {
            token:getCookie('USER')
        },{withCredentials:true
        })
        .then((response)=>{
          if(response.data !== -3){
            console.log(response.data);
            setUser(response.data);
          }else if((response.data === -3) && (localStorage.refreshToken)){
            window.alert('refreshing...');
            axios.post('http://13.125.81.51:3003/apis/auth/refreshToken', {
            refreshToken:localStorage.refreshToken
            },{withCredentials:true
            }).then((res)=>{
              if(res.data !== -3){
                window.alert('refreshed');
                setCookie('USER',res.data.token,{
                path:"/",
                secure:false,
                sameSite:"lax",
              });
              localStorage.setItem('refreshToken',res.data.refreshToken);
              window.location.reload();
              }else{
                removeCookie('USER');
                localStorage.removeItem('refreshToken');
                alert(' .   ');
                window.location.replace('/');
              }
            });
          }
          });
        }
      },[auth()]);

    useEffect(()=>{
      if(user){
      axios.post('http://13.125.81.51:3003/apis/user/getUserDetail', {
      userid:user.user_id
      },{withCredentials:true})
      .then((res)=>{
        setUserDetail(res.data[0]);
      });
    } 
    },[user]);

    function goCommunity(){
    navigate(`/community/${userDetail.myteam}/1`);
    };

    const modifyPost=(postid, e) => {
      e.preventDefault();
      console.log(postid);
      navigate('/modify', {state : postid.toString()});
  }

  const deletePost=(postid, e) => {
    e.preventDefault();
    axios.post('http://13.125.81.51:3003/apis/board/deleteBoard',{
        board_id: postid
    },{
        headers:{
            token:getCookie('USER')
        }
    }).then((response)=>{
    window.alert('.');
    navigate(-1);
    })
  }

    useEffect(()=>{
            axios.post(`http://13.125.81.51:3003/apis/board/getBoardDetail`,{
              board_id:params.postid
            },{withCredentials:true})
            .then((response)=>{
            setPost(response.data[0]);
        })
    },[]);
    
    return (
        <CommunityDetailPresenter
        auth={auth}
        user={user}
        userDetail={userDetail}
        post={post}
        modifyPost = {modifyPost}
        deletePost = {deletePost}
        goCommunity = {goCommunity} />
    )
}

export default CommunityDetailConatiner;