import { useEffect, useState } from 'react';
import GetLogo from 'components/GetLogo';
import SchedulePresenter from './SchedulePresenter';
import { useLocation, useNavigate } from 'react-router';
import moment from 'moment';
// @ts-ignore
import { getCookie, removeCookie, setCookie } from 'Cookie.ts';
import axios from 'axios';
import React from 'react';

let prevNum = 0;
let nextNum = 0;

function ScheduleConatiner () {
    const navigate = useNavigate();
    const location:any = useLocation();
    const [user,setUser] = useState<any>([]);
    const [userDetail,setUserDetail] = useState<any>(null);
    const [matches, setMatch] = useState<any>([]);
   
    let [date, setDate] = useState(moment().format('YYYY-MM-DD'));

    function auth(){
        if(getCookie('USER')){
          return true;
        }else{
          window.alert('');
          window.location.replace('/');
          return false;
        };
      }

      useEffect(()=>{
        if(auth()){
          axios.post('http://13.125.81.51:3003/apis/auth/authToken', {
            token:getCookie('USER')
        },{withCredentials:true
        })
        .then((response)=>{
          if(response.data !== -3){
            console.log(response.data);
            setUser(response.data);
          }else if((response.data === -3) && (localStorage.refreshToken)){
            window.alert('refreshing...');
            axios.post('http://13.125.81.51:3003/apis/auth/refreshToken', {
            refreshToken:localStorage.refreshToken
            },{withCredentials:true
            }).then((res)=>{
              if(res.data !== -3){
                window.alert('refreshed');
                setCookie('USER',res.data.token,{
                path:"/",
                secure:false,
                sameSite:"lax",
              });
              localStorage.setItem('refreshToken',res.data.refreshToken);
              window.location.reload();
              }else{
                removeCookie('USER');
                localStorage.removeItem('refreshToken');
                alert(' .   ');
                window.location.replace('/');
              }
            });
          }
          });
        }
      },[auth()]);

    function goPrevious():void{
        prevNum++;
        if(prevNum>nextNum){
            setDate(moment().clone().subtract(prevNum-nextNum,'days').format('YYYY-MM-DD'));
        }else{
            setDate(moment().clone().add(nextNum-prevNum,'days').format('YYYY-MM-DD'));
        }
    }

    function goNext():void{
        nextNum++;
        if(nextNum>prevNum){
            setDate(moment().clone().add(nextNum-prevNum,'days').format('YYYY-MM-DD'));
        }else{
            setDate(moment().clone().subtract(prevNum-nextNum,'days').format('YYYY-MM-DD'));
        }
    }

    useEffect(()=>{
        if(auth()){
            axios.post('http://13.125.81.51:3003/apis/football/getMatchList',{
                date:date.toString()
            },{
                withCredentials:true
            })
            .then((response)=>{
                setMatch(response.data);
            })
        }
    },[date]);
 
    return (
        <SchedulePresenter
        matches={matches}
        date={date}
        setDate={setDate}
        goPrevious = {goPrevious}
        goNext = {goNext}
        getLogo = {GetLogo} />
    )
}

export default ScheduleConatiner;