import React from 'react';

function GetTeamname(code:string):string {
    var teamName;
    switch(code){
        case '120':
            teamName = ' FC'; break;
        case '121':
            teamName = ' '; break;
        case '129':
            teamName = ''; break;
        case '130':
            teamName = '   '; break;
        case '131':
            teamName = ''; break;
        case '134':
            teamName = ' FC'; break;
        case '136':
            teamName = ' '; break;
        case '138':
            teamName = ' FC'; break;
        case '143':
            teamName = ' '; break;
        case '144':
            teamName = ' '; break;
        case '145':
            teamName = ' FC'; break;
        case '146':
            teamName = ' '; break;
        case '147':
            teamName = ' '; break;
        case '149':
            teamName = ''; break;
        case '150':
            teamName = ' '; break;
        case '158':
            teamName = ''; break;
        case '163':
            teamName = ' '; break;
        case '164':
            teamName = ' FC'; break;
        case '166':
            teamName = ' '; break;
        case '171':
            teamName = ' '; break;
        default:
            teamName = 'NONE'; break;
    }

    return teamName;
}


export default GetTeamname;