import Footer from "components/Footer";
import Header from "components/Header";
import Loading from "components/Loading";
import MainWrapper from "components/MainWrapper";
import NavBar from "components/NavBar";
import { ReactChild, ReactFragment, ReactPortal } from "react";
import styled from "styled-components";
import Teamcode from "components/Teamcode";
import React from "react";

const HeadLine1 = styled.h1`
    font-size:25px;
    margin:0;
    padding:15px;
    width:100%;
`;

const Section = styled.div`
    width:100%;
    padding:10px;
`;

const Select = styled.select`
    width: 100px;
    border: 1px solid #999;
    font-family: inherit;
`;

const Option = styled.option`
    width: 100px;
    border: 1px solid #999;
    font-family: inherit;
`;

const GoDetail = styled.a`
    &:hover{
        background-color:whitesmoke;
    }
    width:100%;
`;

const MyButton = styled.button`
    margin:3px;
`;

function MypagePresenter({
    user,
    userDetail,
    nick,
    setNick,
    nickChange,
    myPost,
    submit,
    myTeam,
    setMyTeam,
    teamChange,
    modifyPost,
    deletePost,
    signOut,
}:{
    user:any,
    userDetail:any,
    nick:string,
    setNick:React.Dispatch<React.SetStateAction<string>>,
    nickChange:(e: any) => void,
    myPost:any,
    submit:() => void,
    myTeam:string,
    setMyTeam:React.Dispatch<React.SetStateAction<string>>,
    teamChange:(e: any) => void,
    modifyPost:any,
    deletePost:any;
    signOut:any;
}){
    return (
        <>
        <Header />
        <NavBar/>
        <MainWrapper>
        <HeadLine1>MyPage</HeadLine1>
        {userDetail ?
        <>
        <Section>
        <h4> ID</h4>
        {userDetail.loginid}
        </Section>
        <Section>
        <h4></h4>
        {userDetail.nickname}<br />
        <input type="text" value={nick} onChange={nickChange}/>
        </Section>
        <Section>  
        <h4> </h4>
        {Teamcode(userDetail.myteam)}<br />
        <Select name="team" value={myTeam} onChange={teamChange}>
        <Option value="0"></Option>
            <Option value="120"></Option>
            <Option value="121"> </Option>
            <Option value="131"></Option>
            <Option value="130"></Option>
            <Option value="129"></Option>
            <Option value="134"></Option>
            <Option value="136"> </Option>
            <Option value="138"></Option>
            <Option value="145"></Option>
            <Option value="144"></Option>
            <Option value="143"></Option>
            <Option value="147"></Option>
            <Option value="146"></Option>
            <Option value="150"></Option>
            <Option value="149"></Option>
            <Option value="158"></Option>
            <Option value="163"> </Option>
            <Option value="166"></Option>
            <Option value="171"></Option>
            <Option value="164"></Option>
        </Select>
        </Section>
        <Section>
        <h4> </h4>
        {
        myPost.length>0 ?
        myPost.map((
            data:
            {
                title: boolean | ReactChild | ReactFragment | ReactPortal | null | undefined;
                board_id: boolean | ReactChild | ReactFragment | ReactPortal | null | undefined;
            }
        )=>(
            <>
            <GoDetail href={`/communitydetail/${userDetail.myteam}/${data.board_id}`}>{data.title}</GoDetail>
            <MyButton onClick={(e)=>{modifyPost(data.board_id?.toString(), e)}}></MyButton>
            <MyButton onClick={(e)=>{deletePost(data.board_id?.toString(), e)}}></MyButton>
            <br />
            </>
        ))
        :
        <Loading/>}
        </Section>
        </>
        :
        <Loading/>
        }
        <Section>
               .
        </Section>
        <MyButton onClick={submit}> </MyButton>
        <MyButton onClick={signOut}> </MyButton>
        </MainWrapper>
        <Footer />
        </>
    );
}

export default MypagePresenter;